<template>
  <el-dialog title="自营特价商品管理" :visible.sync="dialogTableVisible" :before-close="handleClose">
    <el-button size="mini" @click="onSave">保存</el-button>
    <el-table ref="dragTable" row-key="id" :data="gridData" size="mini" highlight-current-row>
      <el-table-column label="序号">
        <template slot-scope="scope">
          <span>{{ scope.$index }}</span>
        </template>
      </el-table-column>
      <el-table-column property="name" label="姓名" />
      <el-table-column label="操作">
        <template slot-scope="scope">
          <el-button size="mini" @click="onDelete(scope)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
  </el-dialog>
</template>

<script>
import Sortable from 'sortablejs'

export default {
  name: 'setZiYingTeJiaGoods',

  props: {
    dialogTableVisible: {
      type: Boolean,
      default: false
    }
  },

  data () {
    return {
      gridData: [],
      sortable: null,
    }
  },

  async created () {
    await this.apiFindOptimizationProd()
  },

  methods: {
    handleClose () {
      this.$emit('close')
    },
    // 点击保存
    async onSave () {
      const prodId = this.gridData.map((item) => {
        return item.id
      })
      const data = {
        prodIds: prodId.join(','),
        type: 1, // 0新增, 1覆盖
      }
      const res = await this.$http.post(this.$api.optimizationProd, data)
      if (res.data.success) {
        this.$message.success('保存成功')
        this.$emit('close')
      } else {
        this.$message.error(res.data.msg)
      }
    },
    // 点击删除
    onDelete (scope) {
      if (this.gridData.length < 9) {
        return this.$message.error('不能小于8个商品')
      } else {
        this.gridData.splice(scope.$index, 1)
      }
    },

    // ---------------------------------
    // 查询自营特价商品
    async apiFindOptimizationProd () {
      const res = await this.$http.get(`${this.$api.findOptimizationProd}/${0}`)
      this.gridData = res.data.data
      this.$nextTick(() => {
        this.setSort()
      })
    },
    setSort() {
      const el = this.$refs.dragTable.$el.querySelectorAll('.el-table__body-wrapper > table > tbody')[0]
      this.sortable = Sortable.create(el, {
        ghostClass: 'sortable-ghost', // Class name for the drop placeholder,
        setData: function(dataTransfer) {
          // to avoid Firefox bug
          // Detail see : https://github.com/RubaXa/Sortable/issues/1012
          dataTransfer.setData('Text', '')
        },
        onEnd: evt => {
          const targetRow = this.gridData.splice(evt.oldIndex, 1)[0]
          this.gridData.splice(evt.newIndex, 0, targetRow)
        }
      })
    },
  }
}
</script>

