<template>
  <el-dialog
    title="添加家电节商品"
    :visible.sync="dialogVisible"
    width="30%"
    :before-close="handleClose"
  >
    <el-form :model="form">
      <el-form-item label="选择商品：">
        <el-select v-model="form.prodIds" filterable multiple placeholder="请选择">
          <el-option
            v-for="item in goodslist"
            :key="item.id"
            :label="item.name"
            :value="item.id"
          >
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="已选商品：">
        <el-table :data="applianceGoods">
          <el-table-column prop="name" label="名称" />
        </el-table>
      </el-form-item>
      <el-form-item>
        <el-button @click="onSave">添加</el-button>
      </el-form-item>
    </el-form>
  </el-dialog>
</template>

<script>
export default {
  props: {
    dialogVisible: {
      type: Boolean,
      defalut: false
    }
  },
  
  data () {
    return {
      form: {
        prodIds: '',
      },
      goodslist: [],
      applianceGoods: [],
    }
  },

  async created () {
    await this.findOptimizationProd()
    await this.apiProductQuery()
  },
  
  methods: {
    handleClose () {
      this.$emit('close')
    },
    // 点击添加
    async onSave () {
      if (this.form.prodIds.length < 4) return this.$message.error('家电节商品不能少于4个')
      const data = {
        prodIds: this.form.prodIds.join(',')
      }
      const res = await this.$http.post(this.$api.homeWindow, data)
      if (res.data.success) {
        this.$message.success('添加成功')
        await this.findOptimizationProd()
        this.handleClose()
      } else {
        this.$message.error(res.data.msg)
      }
    },

    // ---------------------------------------------------------------------
    // 查询商品列表
    async apiProductQuery () {
      const data = {
        pageSize: 9999,
      }
      const res = await this.$http.post(this.$api.productQuery, data)
      this.goodslist = res.data.data.list
    },
    // 查询已选商品
    async findOptimizationProd () {
      const res = await this.$http.get(`${this.$api.findOptimizationProd}/${2}`)
      this.applianceGoods = res.data.data
    }
  }
}
</script>

