<template>
  <div>
    <layout-box one="商品管理" two="商城列表">
      <el-card shadow="never" class="mb-1">
        <div>
          <i class="el-icon-search"></i>
          <span>筛选搜索</span>
          <el-button style="float: right" type="primary" size="small" @click="handleSearchList()">查询结果</el-button>
          <el-button style="float:right;margin-right: 15px" size="small" @click="handleResetSearch">重置</el-button>
        </div>
        <div class="mt-1">
          <el-form inline size="small" label-width="140px" :model="queryInfo">
            <el-form-item label="输入搜索：">
              <el-input v-model="queryInfo.name" style="width: 203px" placeholder="商品名称" clearable @clear="fetch"></el-input>
            </el-form-item>
            <el-form-item label="商品货号：">
              <el-input v-model="queryInfo.code" style="width: 203px" placeholder="商品货号"></el-input>
            </el-form-item>
            <el-form-item label="上架状态：">
              <el-select v-model="queryInfo.state" placeholder="请选择">
                <el-option label="上架" value="1"></el-option>
                <el-option label="已下架" value="0"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="审核状态：">
              <el-select v-model="queryInfo.approvalStatus">
                <el-option label="已审批" value="1"></el-option>
                <el-option label="未审批" value="0"></el-option>
                <el-option label="驳回" value="2"></el-option>
              </el-select>
            </el-form-item>
          </el-form>
        </div>
      </el-card>
      <router-link v-if="btnPermissions.indexOf('商品新增按钮') !== -1" to="/commo/add">
        <el-button type="danger" size="medium" icon="el-icon-plus">新增</el-button>
      </router-link>
      <div v-if="btnPermissions.indexOf('商品列表更多菜单按钮') !== -1" class="ml-1" style="float: right">
        <!-- <el-button type="danger" class="ml-1" size="medium" :disabled="sels.length === 0" @click="deleteFileOrDirectory(sels)">批量删除</el-button> -->
        <el-dropdown v-if="HomePageClassificationQueryList.length !== 0" class="mb-1" trigger="click" @command="unHandleCommand">
          <el-button size="medium">
            取消菜单<i class="el-icon-arrow-down el-icon--right"></i>
          </el-button>
          <el-dropdown-menu slot="dropdown" style="width: 100px">
            <el-dropdown-item :key="1" :command="1">取消销量</el-dropdown-item>
            <el-dropdown-item v-for="item in HomePageClassificationQueryList" :key="item.id" :command="item.id">{{ item.name }}</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </div>
      <div v-if="btnPermissions.indexOf('自营特价商品') !== -1" style="float: right">
        <el-dropdown v-if="HomePageClassificationQueryList.length !== 0" class="mb-1" trigger="click" @command="setZiYingTeJiaGoodsCommand">
          <el-button size="medium">
            自营特价商品<i class="el-icon-arrow-down el-icon--right"></i>
          </el-button>
          <el-dropdown-menu slot="dropdown" style="width: 130px">
            <el-dropdown-item :command="0">新增</el-dropdown-item>
            <el-dropdown-item :command="1">特价商品管理</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </div>
      <div style="float: right">
        <el-button v-if="btnPermissions.indexOf('设置菜单') !== -1" class="mb-1 mr-1" size="medium" @click="$router.push('/commodity/set_cai_dan')">设置菜单</el-button>
      </div>
      <div style="float: right">
        <el-button v-if="btnPermissions.indexOf('设置橱窗商品') !== -1" class="mb-1 mr-1" size="medium" @click="setShowcase">设置橱窗商品</el-button>
      </div>
      <div style="float: right">
        <el-button v-if="btnPermissions.indexOf('设置浏览排序') !== -1" class="mb-1 mr-1" size="medium" @click="setSort">设置浏览排序</el-button>
      </div>
      <div v-if="btnPermissions.indexOf('商品列表更多菜单按钮') !== -1" style="float: right">
        <!-- <el-button type="danger" class="ml-1" size="medium" :disabled="sels.length === 0" @click="deleteFileOrDirectory(sels)">批量删除</el-button> -->
        <el-dropdown v-if="HomePageClassificationQueryList.length !== 0" class="mb-1 mr-1" trigger="click" @command="handleCommand">
          <el-button size="medium">
            更多菜单<i class="el-icon-arrow-down el-icon--right"></i>
          </el-button>
          <el-dropdown-menu slot="dropdown" style="width: 100px">
            <el-dropdown-item :key="1" :command="1">添加销量</el-dropdown-item>
            <el-dropdown-item v-for="item in HomePageClassificationQueryList" :key="item.id" :command="item.id">{{ item.name }}</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </div>
      <div style="float: right">
        <el-button v-if="btnPermissions.indexOf('设置蜗视家电节') !== -1" class="mb-1 mr-1" size="medium" @click="setJiaDianGoodssVisible = true">设置蜗视家电节</el-button>
      </div>
      <!-- 商品列表 -->
      <el-table v-loading="loading" class="mt-2" border :data="goodslist" style="width: 100%" max-height="500" @selection-change="selsChange">
        <el-table-column type="selection" width="40" align="center" />
        <el-table-column label="浏览排序" align="center">
          <template slot-scope="scope">
            <p>{{ scope.row.sorting }}</p>
          </template>
        </el-table-column>
        <el-table-column prop="name" label="商品图片" width="125" align="center">
          <template slot-scope="scope">
            <el-image style="width: 100px; height: 100px" :src="scope.row.imageUrl" />
          </template>
        </el-table-column>
        <el-table-column label="商品名称" align="center">
          <template slot-scope="scope">
            <p>{{ scope.row.name }}</p>
          </template>
        </el-table-column>
        <el-table-column label="分类" align="center">
          <template slot-scope="scope">
            {{ scope.row.categoryName }}
          </template>
        </el-table-column>
        <el-table-column align="center" label="销量">
          <template slot-scope="scope">
            {{ scope.row.sales || 0 }}
          </template>
        </el-table-column>
        <el-table-column v-if="btnPermissions.indexOf('虚假销量') !== -1" align="center" label="虚假销量">
          <template slot-scope="scope">
            {{ scope.row.virtualAmount || 0 }}
          </template>
        </el-table-column>
        <el-table-column v-if="btnPermissions.indexOf('设置橱窗商品') !== -1" align="center" label="橱窗商品">
          <template slot-scope="scope">
            {{ scope.row.homeVideo === 1 ? '是' : '否' }}
          </template>
        </el-table-column>
        <template v-if="btnPermissions.indexOf('商品列表更多菜单按钮') !== -1">
          <el-table-column v-for="item in HomePageClassificationQueryList" :key="item.id" align="center" :label="item.name">
            <template slot-scope="scope">
              <div v-for="listNameItem in scope.row.listName" :key="listNameItem.id">
                <i v-if="listNameItem.id === item.id" class="iconfont icon">&#xe605;</i>
              </div>
            </template>
          </el-table-column>
        </template>
        <el-table-column align="center" label="状态">
          <template slot-scope="scope">
            <span v-if="scope.row.state === 1" style="color: #40ccae">上架</span><span
              v-else-if="scope.row.state === 0"
              style="color: #e39025"
            >已下架</span>
          </template>
        </el-table-column>
        <el-table-column label="审核状态" align="center">
          <template slot-scope="scope">
            <span
              v-if="scope.row.approvalStatus === 0"
              style="color: #666666"
            >未审批</span>
            <span
              v-if="scope.row.approvalStatus === 1"
              style="color: #40ccae"
            >已审批</span>
            <span
              v-if="scope.row.approvalStatus === 2"
              style="color: #e02929"
            >驳回</span>
          </template>
        </el-table-column>
        <el-table-column v-if="btnPermissions.indexOf('商品上下架按钮') !== -1" label="标签" align="center">
          <template slot-scope="scope">
            <p>
              上架：
              <el-switch
                v-model="scope.row.state"
                :disabled="scope.row.approvalStatus == '0'"
                :active-value="1"
                :inactive-value="0"
                @change="handlePublishStatusChange(scope.id, scope.row)"
              />
            </p>
          </template>
        </el-table-column>
        <el-table-column prop="address" align="center" label="操作" width="179">
          <template slot-scope="scope">
            <el-link
              v-if="btnPermissions.indexOf('商品编辑按钮') !== -1"
              :underline="false"
              type="primary"
              @click="onEdit(scope)"
            >
              编辑
            </el-link>
            <el-link
              type="success"
              :underline="false"
              @click="onDetail(scope)"
            >
              详情
            </el-link>
            <el-link
              v-if="btnPermissions.indexOf('商品删除按钮') !== -1"
              type="danger"
              :underline="false"
              class="text-white"
              @click="removeUserById(scope.row.id)"
            >删除</el-link>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        class="mt-1"
        :current-page.sync="queryInfo.pageNo"
        :page-sizes="[2, 5, 10, 20]"
        :page-size="queryInfo.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
      />
    </layout-box>

    <el-dialog
      title="添加虚假销量"
      :visible.sync="salesDialogVisible"
      width="30%"
    >
      <span>数量：</span>
      <el-input v-model="amount"></el-input>
      <span slot="footer" class="dialog-footer">
        <el-button @click="salesDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="onSalesSubmit">确 定</el-button>
      </span>
    </el-dialog>

    <el-dialog title="设置浏览排序" :visible.sync="selectDialogFormVisible">
      <el-form :model="sortingForm">
        <el-form-item label="序号">
          <el-input v-model="sortingForm.sorting"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="selectDialogFormVisible = false">取消</el-button>
        <el-button type="primary" @click="onSaveSort">保存</el-button>
      </div>
    </el-dialog>

    <el-dialog title="设置浏览排序" :visible.sync="selectDialogFormVisible">
      <el-form :model="sortingForm">
        <el-form-item label="序号">
          <el-input v-model="sortingForm.sorting"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="selectDialogFormVisible = false">取消</el-button>
        <el-button type="primary" @click="onSaveSort">保存</el-button>
      </div>
    </el-dialog>

    <setZiYingTeJiaGoods-cmp v-if="setZiYingTeJiaGoodsVisible" :dialogTableVisible="setZiYingTeJiaGoodsVisible" @close="onClose" />
    <setJiaDianGoods-cmp :dialogVisible="setJiaDianGoodssVisible" @close="onCloseSetJiaDianGoodss" />
  </div>
</template>

<script>
import { MIXINS_BTNPERMISSIONS_DATA } from '@/mixins/data.js'
import { MIXINS_APIPRODUCTQUERYCHOOSE_API, MIXINS_PRODUCTBATCHVIP_API } from '@/mixins/api.js'
import setZiYingTeJiaGoodsCmp from './cmp/set_zi_ying_te_jia_goods.vue'
import setJiaDianGoodsCmp from './cmp/set_jia_dian_goods.vue'

const defaultListQuery = {
  pageNo: 1,
  pageSize: 10,
  name: null,
  code: null,
  state: null,
  approvalStatus: null
}
export default {
  data() {
    return {
      sels: [], // 选中的值显示
      model: {},
      approve: {
        approveReason: '',
        approveStatus: 0
      },
      examine: false,
      editSkuInfo: {
        dialogVisible: false,
        productId: null,
        productSn: '',
        productAttributeCategoryId: null,
        stockList: [],
        productAttr: [],
        keyword: null
      },
      // 加载
      loading: true,
      // 商品分页
      query: '',
      queryInfo: Object.assign({}, defaultListQuery),
      // 商品列表
      goodslist: [],
      state: null,
      // 总数据条数
      total: 0,
      // 0-未审批 1-已审批 2-驳回
      queryInf: {
        approveStatus: 1,
        id: ''
      },
      queryInf1: {
        approveStatus: 2,
        id: ''
      },
      HomePageClassificationQueryList: [], // 商城标签列表
      sortingForm: {
        sorting: '', // 商品排序的序号
      },

      // ------------------------------------------------------
      // 假销量的添加窗口
      salesDialogVisible: false, // 假销量的添加窗口隐藏关闭
      amount: '', // 添加假销量的数据
      selectDialogFormVisible: false, // 排序的窗口
      setZiYingTeJiaGoodsVisible: false, // 自营特价商品窗口
      setJiaDianGoodssVisible: false, // 家电节商品
    }
  },

  components: {
    setZiYingTeJiaGoodsCmp,
    setJiaDianGoodsCmp
  },

  mixins: [
    MIXINS_BTNPERMISSIONS_DATA,
    MIXINS_APIPRODUCTQUERYCHOOSE_API,
    MIXINS_PRODUCTBATCHVIP_API,
  ],

  methods: {
    init () {
      this.fetch()
      // this.productQueryChoose()
    },
    // 点击编辑
    onEdit (scope) {
      var { href } = this.$router.resolve({
        path: `/commo/edit/${scope.row.id}`,
      })
      window.open(href)
    },
    // 点击详情
    onDetail (scope) {
      var { href } = this.$router.resolve({
        path: `/commo/audit/${scope.row.id}`,
      })
      window.open(href)
    },
    // 审核按钮
    handleShowVerifyDetail(id) {
      this.examine = true
      this.queryInf.id = id
      this.queryInf1.id = id
    },
    //  商品列表
    fetch () {
      this.loading = true
      this.queryInfo.type = 0
      this.$http.post(this.$api.productQuery, this.queryInfo).then(res => {
        this.loading = false
        this.goodslist = res.data.data.list
        this.total = res.data.data.total
      })
    },
    handleSizeChange(pageSize) {
      this.queryInfo.pageSize = pageSize
      this.fetch()
    },
    handleCurrentChange(pageNo) {
      this.queryInfo.pageNo = pageNo
      this.fetch()
    },
    // 状态更新
    handlePublishStatusChange(id, row) {
      this.$http
        .post(`/woho/product/head/upOrDown/${row.id}/${row.state}`)
        .then(res => {
          if (res.data.code !== 200) return this.$message.error('更新失败')
          this.$message.success('更新成功')
          this.fetch()
        })
    },
    // 商品删除
    removeUserById(id) {
      this.$confirm(`是否确定要删除该商品 "是否继续"`, '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.$http.delete(`/woho/product/head/delete/` + id).then(res => {
          this.$message({
            type: 'success',
            message: '删除成功!'
          })
          this.fetch()
        })
      })
    },
    // 搜索
    handleSearchList() {
      this.queryInfo.pageNo = 1
      this.fetch()
    },
    handleResetSearch() {
      this.queryInfo = Object.assign({}, defaultListQuery)
      this.fetch()
    },
    // 点击选择表格的列表
    selsChange(sels) {
      this.sels = sels
    },
    deleteFileOrDirectory() {
      let arr = this.sels.map(item => item.id).join()// 获取所有选中行的id组成的字符串，以逗号分隔
      console.log(arr)
      this.$confirm('此操作将删除所有商品, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        const ids = arr
        this.$http.delete(`/woho/product/head/batchDelete/` + ids).then(res => {
          this.$message({
            message: '删除成功',
            type: 'success',
            duration: 1000
          })
          this.fetch()
        })
      })
    },
    // 选择设为精选还是设为vip
    async handleCommand (command) {
      if (this.sels.length === 0) return this.$message.error('请先选择商品')
      if (command === 1) {
        this.salesDialogVisible = true
      } else {
        await this.apiProductBatchChooseApi(command)
      }
    },
    // 点击取消菜单标签
    async unHandleCommand (command) {
      if (this.sels.length === 0) return this.$message.error('请先选择商品')
      if (command === 1) {
        const prodId = this.sels.map((item) => item.id)
        const res = await this.$http.get(`${this.$api.updateVirtual}/${prodId.join(',')}/0`)
        if (res.data.success) {
          this.$message.success('取消成功')
          this.fetch()
        } else {
          this.$message.error(res.data.msg)
        }
      } else {
        await this.apiUpdateClassificationProd(command)
      }
    },
    // 设置橱窗商品
    async setShowcase () {
      if (this.sels.length === 0) return this.$message.error('请先选择商品')
      console.log(this.sels.length)
      if (this.sels.length > 1) return this.$message.error('商品不能多选')
      let prodId = ''
      this.sels.forEach((item) => {
        prodId = item.id
      })
      const res = await this.$http.get(`${this.$api.updateprodVido}/${prodId}`)
      if (res.data.success) {
        this.$message.success('添加橱窗商品成功')
        this.fetch()
      } else {
        this.$message.error(res.data.msg)
      }
    },
    // 添加假销量确定按钮
    async onSalesSubmit () {
      const prodId = this.sels.map((item) => item.id)
      const res = await this.$http.get(`${this.$api.updateVirtual}/${prodId.join(',')}/${this.amount}`)
      if (res.data.success) {
        this.$message.success('添加成功')
        this.salesDialogVisible = false
        this.fetch()
      } else {
        this.$message.error(res.data.msg)
      }
    },
    // 点击设置排序
    setSort () {
      if (this.sels.length === 0) return this.$message.error('请先选择商品')
      if (this.sels.length > 1) return this.$message.error('商品不能多选')
      this.selectDialogFormVisible = true
    },
    // 点击排序的保存
    async onSaveSort () {
      const prodId = this.sels[0].id
      const res = await this.$http.get(`${this.$api.updateSorting}/${prodId}/${this.sortingForm.sorting}`)
      if (res.data.success) {
        this.fetch()
        this.selectDialogFormVisible = false
        this.$message.success('设置成功')
      } else {
        this.$message.error(res.data.msg)
      }
    },
    // 点击自营特价商品
    async setZiYingTeJiaGoodsCommand (e) {
      if (e === 1) {
        this.setZiYingTeJiaGoodsVisible = true
      } else {
        if (this.sels.length === 0) return this.$message.error('请先选择商品')
        const prodId = this.sels.map((item) => item.id)
        await this.apiOptimizationProd(prodId.join(','))
      }
    },
    // 关闭子组件窗口
    onClose () {
      this.setZiYingTeJiaGoodsVisible = false
    },
    // 家电节商品窗口关闭
    onCloseSetJiaDianGoodss () {
      this.setJiaDianGoodssVisible = false
    },

    // --------------------------------------------
    // 修改是否是精选
    async apiProductBatchChooseApi (classificationId) {
      const prodId = this.sels.map((item) => item.id)
      const data = {
        prodId: prodId.join(','),
        classificationId: classificationId
      }
      const res = await this.$http.post(this.$api.addClassificationProd, data)
      if (res.data.success) {
        this.$message.success('操作成功')
        this.fetch()
      } else {
        this.$message.error(res.data.msg)
      }
    },
    // 取消菜单标签
    async apiUpdateClassificationProd (classificationId) {
      const prodId = this.sels.map((item) => item.id)
      const res = await this.$http.get(`${this.$api.updateClassificationProd}/${classificationId}/${prodId}`)
      if (res.data.success) {
        this.$message.success('操作成功')
        this.fetch()
      } else {
        this.$message.error(res.data.msg)
      }
    },
    // 查询商城商品标签分页
    async apiHomePageClassificationQuery () {
      const data = {
        pageNo: 1,
        pageSize: 999,
        enableStatus: 0, // 查询所有启用的标签
      }
      const res = await this.$http.post(this.$api.HomePageClassificationQuery, data)
      this.HomePageClassificationQueryList = res.data.data.list
    },
    // 新增自营特价商品
    async apiOptimizationProd (prodId) {
      const data = {
        prodIds: prodId,
        type: 0, // 0新增, 1覆盖
      }
      const res = await this.$http.post(this.$api.optimizationProd, data)
      if (res.data.success) {
        this.$message.success('添加成功')
        this.fetch()
      } else {
        this.$message.error(res.data.msg)
      }
    },
  },

  created() {
    this.init()
    this.apiHomePageClassificationQuery()
  },
}
</script>

<style lang="scss" scoped>
.el-link {
  display: inline-block;
  width: 25%;
}

.box-card {
  min-width: 1300px;
}
.btns {
  color: #fff;
}
.iconfont{
  color: #ff9f00;
  font-size: 25px;
}
</style>
